import {Component, Inject, OnInit} from '@angular/core';
import {CommunicationService} from '../../../services/Communication.service';
import {Example} from '../../../../../domain/models/example/RootObject';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {ProfileRequest} from '../../../../../domain/models/profile/profilerequest.model';
import {PcPlansToken} from '../../../services/plans/pcplans.provider';
import {BoPCPlan} from '../../../../../domain/models/plans/bopcplan.model';
import {RequestPlan} from '../../../../../domain/models/plans/requestplan.model';
import {ScrollSpyService} from 'ngx-scrollspy';
import {Router} from '@angular/router';
import {RequestActivePlan} from '../../../../../domain/models/plans/requestactiveplan.model';

@Component({
  selector: 'app-pcplan',
  templateUrl: './pcplan.component.html',
  styleUrls: ['./pcplan.component.scss']
})
export class PcPlanComponent implements OnInit {

  public listBoPCPlan: BoPCPlan[] = [];
  public listBoPCPlanTemp: BoPCPlan[] = [];

  public titleMorePlans: string;
  public message: string;
  public messagePlan1: number;

  constructor(@Inject(PcPlansToken) private boPCPlanIRepository: IRepository<RequestPlan>, private router: Router) {
    this.loadPlans();
    this.changeTitleNameMore();
  }

  ngOnInit() {

  }

  private changeTitleNameMore() {
    this.titleMorePlans = 'VER TODOS LOS PLANES';
  }

  private changeNoTitleNameMore() {
    this.titleMorePlans = 'VER MENOS PLANES';
  }


  private twoOnePlan() {
    this.listBoPCPlan = [];
    this.listBoPCPlan.push(this.listBoPCPlanTemp[0]);
    this.listBoPCPlan.push(this.listBoPCPlanTemp[1]);
  }

  private seeAllPlans() {
    this.listBoPCPlan = [];
    this.listBoPCPlan = this.listBoPCPlanTemp;
  }

  morePlans() {
    if (this.listBoPCPlan.length == this.listBoPCPlanTemp.length) {
      this.twoOnePlan();
      this.changeTitleNameMore();
    } else {
      this.seeAllPlans();
      this.changeNoTitleNameMore();
    }
  }


  selected(plan: BoPCPlan) {
    //console.log(plan);
    if (plan.selected == 0) {
      plan.selected = 1;
    } else {
      plan.selected = 0;
    }
    plan.total = plan.price * plan.count;

  }

  addPlan(plan: BoPCPlan) {
    plan.count = plan.count + 1;
    plan.total = plan.price * plan.count;
  }

  removePlan(plan: BoPCPlan) {
    if (plan.count > 2) {
      plan.count = plan.count - 1;
      plan.total = plan.price * plan.count;
    }
  }

  loadPlans() {
    var request = new RequestPlan();
    this.boPCPlanIRepository.create(request).subscribe(response => {
      this.listBoPCPlanTemp = response.content;

      sessionStorage.setItem('listBoPCPlan', JSON.stringify(this.listBoPCPlan));
      if (this.listBoPCPlanTemp != null && this.listBoPCPlanTemp.length > 0) {
        this.twoOnePlan();
      }
    });
  }

  increasePlan(plan: BoPCPlan) {
    if (plan.totalSearch == 1) {
      if (plan.count == 19) {
        alert('Hay un plan mejor, el de 20');
        return;
      }
    }
    if (plan.totalSearch == 20) {
      if (plan.count == 20) {
        alert('Hay un plan mejor, el de 150');
        return;
      }
    }
    plan.count = plan.count + 1;
  }

  decreasePlan(plan: BoPCPlan) {
    if (plan.count > 2) {
      plan.count = plan.count - 1;
    }
  }


  siguiente() {

    var listSelected: BoPCPlan[] = this.listBoPCPlan.filter(item => item.selected == 1);
    if (listSelected != null && listSelected.length > 0) {
      this.router.navigate(['products/pcplansumary'], {state: {listBoPCPlan: listSelected}});
    }else{
      alert("seleccione al menos un plan");
    }
  }

}
