import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface'; ;
import {BoPCPlan} from '../../../../domain/models/plans/bopcplan.model';
import {PayPlanModel} from '../../../../domain/models/plans/payplan.model';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class PayPlanService implements IRepository<PayPlanModel> {

  constructor(private http: HttpClient) {
  }

  public create(values : PayPlanModel): Observable<RootObject> {    
    var response = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/public/plans/pc/payplan/`,values);
    return response;
  }
}
