import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {PcPlansService} from './pcplans.service';
import {RequestPlan} from '../../../../domain/models/plans/requestplan.model';

export const PcPlansToken = new InjectionToken<IRepository<RequestPlan>>('pcplans.service');

export class PcPlansProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<RequestPlan>> = PcPlansToken;
  public deps: any[] = [PcPlansService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<RequestPlan>[]) => {
    return deps[0]
  };
}
