import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {CuponRequestService} from './cuponrequest.service';
import {CuponRequest} from '../../../../domain/models/plans/cuponrequest.model';

export const CuponRequestToken = new InjectionToken<IRepository<CuponRequest>>('cuponrequest.service');

export class CuponRequestProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<CuponRequest>> = CuponRequestToken;
  public deps: any[] = [CuponRequestService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<CuponRequest>[]) => {
    return deps[0]
  };
}
