import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";


import {PayPlanService} from './payplan.service';
import {PayPlanModel} from '../../../../domain/models/plans/payplan.model';

export const PayPlanToken = new InjectionToken<IRepository<PayPlanModel>>('payplan.service');

export class PayPlanProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<PayPlanModel>> = PayPlanToken;
  public deps: any[] = [PayPlanService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<PayPlanModel>[]) => {
    return deps[0]
  };
}
