import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import { BranchModel } from '../../../../domain/models/profile/branchmodel.model';
import {BoPCPlan} from '../../../../domain/models/plans/bopcplan.model';
import {RequestPlan} from '../../../../domain/models/plans/requestplan.model';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class PcPlansService implements IRepository<RequestPlan> {

  constructor(private http: HttpClient) {
  }

  public create(values : RequestPlan): Observable<RootObject> {    
    var response = this.http.get<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/public/plans/pc/plans/`);
    return response;
  }
}
