import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexProfileComponent } from '../profile/index/indexprofile.component';
import { IndexEditComponent } from '../profile/indexedit/indexedit.component';
import {PcPlanComponent} from './pcplan/pcplan.component';
import {SumaryPlanComponent} from './sumaryplan/sumaryplan.component';


const routes: Routes = [
  { path:'pcplanselection', component : PcPlanComponent},
  { path: 'pcplansumary', component: SumaryPlanComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class PlanRoutingModule { }


